import React from 'react';
import Footer from './Footer';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

export default function WillingnessQuestion2(props) {

	const [willingnessQuestion2, setWillingnessQuestion2] = React.useState("");
	const STATE = props.location.state
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const [loading, setLoading] = React.useState(true);

	const RadioInputOptions = [
		{ key: '1', name: 'Moving to safe instruments completely and securing the corpus' },
		{ key: '2', name: 'No changes in portfolio. Stay invested for 2 more years for extra corpus' },
		{ key: '3', name: 'Moving half of the corpus to safe instruments and keeping the other half invested thereby neither completely losing out on potential returns nor risking the entire corpus' },
	]

	React.useEffect(async () => {
		await db.get(db.model.riskProfile).then(p => {
			if (p) {
				setWillingnessQuestion2(p.willingnessQuestion2 || "")
			} else {
				setWillingnessQuestion2("")
			}
		})
		setLoading(false)
	}, [])

	const checkValidation = () => {
		if (willingnessQuestion2) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [willingnessQuestion2])

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={81} className="progress-bar-custom" />
			<div className="dataContainer" style={{ paddingTop: '42px' }}>
				{
					loading ?
						<ActivityLoaderMintbox />
						:
						<>
							<div className="header1dark">
								Your target retirement corpus has been achieved 2 years in advance.
								</div>
							<div className="header1dark" style={{ marginTop: "6%" }}>What will be your plan of action?</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row', marginTop: "6%" }}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setWillingnessQuestion2(val.target.value)
											}}
											className="bodyTextDark"
											checked={willingnessQuestion2 === obj.key}
										/>
										<label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/risk-profile/result"				
				state={{...STATE,willingnessQuestion2}}
				saveData={{ willingnessQuestion2 }}
				buttonText="Confirm"
				nextDisabled={nextDisabled}
				/>
		</div>
	)
}
